.form{
  margin-bottom: 2rem;
  margin-top: 2rem;
}
.perdeu{
  display: inline-block;
  color: #666;
  padding: 0.5rem 0;
  line-height: 1;
}
.perdeu::after{
  content: '';
  height: 2px;
  width: 100%;
  background-color: currentColor;
  display: block;
}
.cadastro{
  margin-top: 4rem;
}
.cadastro p{
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.subtitle{
  font-family: var(--type-code2);
  line-height: 1;
  font-size: 2rem;
}
.subtitle::after{
  content: '';
  display: block;
  background: #ddd;
  height: .5rem;
  width: 3rem;
  border-radius: .2rem;
}